"use client";

import { UploadForm } from "@/components/UploadForm";
import { FileText, Users, Download, AlertCircle, Send, Trash2, Eye, EyeOff } from "lucide-react";
import { useEffect, useState } from "react";
import { collection, getCountFromServer, addDoc, serverTimestamp, query, orderBy, onSnapshot, doc, updateDoc, deleteDoc } from "firebase/firestore";
import { db } from "@/lib/firebase";

function NoticeForm() {
    const [title, setTitle] = useState("");
    const [category, setCategory] = useState("General");
    const [desc, setDesc] = useState("");
    const [posting, setPosting] = useState(false);

    const handlePost = async (e: React.FormEvent) => {
        e.preventDefault();
        if (!title || !desc) return;
        setPosting(true);
        try {
            await addDoc(collection(db, "notices"), {
                title,
                category,
                description: desc,
                createdAt: serverTimestamp(),
                priority: category === 'Urgent' ? 'High' : 'Normal'
            });
            setTitle("");
            setDesc("");
            alert("Notice posted!");
        } catch (err) {
            console.error(err);
            alert("Failed to post notice");
        } finally {
            setPosting(false);
        }
    };

    return (
        <form className="space-y-4" onSubmit={handlePost}>
            <div>
                <label className="mb-2 block text-sm font-medium">Title</label>
                <input value={title} onChange={e => setTitle(e.target.value)} type="text" placeholder="Notice Title" className="w-full rounded-lg border border-input bg-transparent px-3 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500/20" />
            </div>
            <div>
                <label className="mb-2 block text-sm font-medium">Category</label>
                <select value={category} onChange={e => setCategory(e.target.value)} className="w-full rounded-lg border border-input bg-transparent px-3 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500/20">
                    <option>General</option>
                    <option>Urgent</option>
                    <option>Holiday</option>
                    <option>Exam</option>
                </select>
            </div>
            <div>
                <label className="mb-2 block text-sm font-medium">Description</label>
                <textarea value={desc} onChange={e => setDesc(e.target.value)} rows={3} placeholder="Notice details..." className="w-full rounded-lg border border-input bg-transparent px-3 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500/20" />
            </div>
            <button disabled={posting} className="flex items-center gap-2 rounded-lg bg-zinc-900 px-4 py-2 text-sm font-medium text-white hover:bg-zinc-800 dark:bg-zinc-100 dark:text-zinc-900">
                <Send className="h-4 w-4" />
                {posting ? "Posting..." : "Post Notice"}
            </button>
        </form>
    );
}

function NoticeList() {
    const [notices, setNotices] = useState<any[]>([]);

    // Real-time listener for notices
    useEffect(() => {
        const q = query(collection(db, "notices"), orderBy("createdAt", "desc"));
        const unsubscribe = onSnapshot(q, (snapshot) => {
            setNotices(snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() })));
        });
        return () => unsubscribe();
    }, []);

    const toggleVisibility = async (id: string, currentStatus: boolean) => {
        try {
            await updateDoc(doc(db, "notices", id), { visible: !currentStatus });
        } catch (err) {
            console.error(err);
        }
    }

    const deleteNotice = async (id: string) => {
        if (!confirm("Are you sure you want to delete this notice?")) return;
        try {
            await deleteDoc(doc(db, "notices", id));
        } catch (err) {
            console.error(err);
        }
    }

    return (
        <div className="space-y-3">
            {notices.map((notice) => (
                <div key={notice.id} className="flex items-center justify-between rounded-lg border border-zinc-100 bg-zinc-50 p-3 dark:border-zinc-800 dark:bg-zinc-900/50">
                    <div>
                        <p className="font-medium">{notice.title}</p>
                        <p className="text-xs text-muted-foreground">{notice.category}</p>
                    </div>
                    <div className="flex gap-2">
                        <button
                            onClick={() => toggleVisibility(notice.id, notice.visible)}
                            className={`rounded p-2 text-xs font-bold transition-colors ${notice.visible === false ? 'bg-zinc-200 text-zinc-500' : 'bg-green-100 text-green-600'}`}
                            title={notice.visible === false ? "Show Notice" : "Hide Notice"}
                        >
                            {notice.visible === false ? <EyeOff className="h-4 w-4" /> : <Eye className="h-4 w-4" />}
                        </button>
                        <button
                            onClick={() => deleteNotice(notice.id)}
                            className="rounded p-2 text-red-500 hover:bg-red-50 dark:hover:bg-red-900/10"
                            title="Delete Notice"
                        >
                            <Trash2 className="h-4 w-4" />
                        </button>
                    </div>
                </div>
            ))}
            {notices.length === 0 && <p className="text-sm text-muted-foreground">No notices found.</p>}
        </div>
    )
}

export default function AdminPage() {
    const [userCount, setUserCount] = useState<number | string>("...");

    useEffect(() => {
        async function fetchStats() {
            try {
                const coll = collection(db, "users");
                const snapshot = await getCountFromServer(coll);
                setUserCount(snapshot.data().count);
            } catch (error) {
                console.error("Error fetching user count", error);
                setUserCount("Err");
            }
        }
        fetchStats();
    }, []);

    return (
        <div className="space-y-8">
            {/* Header */}
            <div>
                <h1 className="text-3xl font-bold tracking-tight">Dashboard</h1>
                <p className="text-muted-foreground">Manage resources, notices, and system status.</p>
            </div>

            {/* Stats Cards */}
            <div className="grid gap-6 sm:grid-cols-2 lg:grid-cols-4">
                {[
                    { label: "Total Resources", value: "1,240", icon: FileText, color: "text-blue-500" },
                    { label: "Active Users", value: userCount, icon: Users, color: "text-green-500" },
                    { label: "Total Downloads", value: "12k", icon: Download, color: "text-purple-500" },
                    { label: "Pending Issues", value: "3", icon: AlertCircle, color: "text-red-500" },
                ].map((stat, i) => (
                    <div key={i} className="rounded-xl border border-zinc-200 bg-white p-6 shadow-sm dark:border-zinc-800 dark:bg-zinc-900">
                        <div className="flex items-center justify-between">
                            <div>
                                <p className="text-sm font-medium text-muted-foreground">{stat.label}</p>
                                <h3 className="text-2xl font-bold">{stat.value}</h3>
                            </div>
                            <stat.icon className={`h-8 w-8 ${stat.color} opacity-20`} />
                        </div>
                    </div>
                ))}
            </div>

            <div className="grid gap-8 lg:grid-cols-3">
                {/* Upload Column */}
                <div className="space-y-8 lg:col-span-2">
                    <UploadForm />

                    {/* Notice Posting Form */}
                    <div className="rounded-xl border border-zinc-200 bg-white p-6 shadow-sm dark:border-zinc-800 dark:bg-zinc-900">
                        <h3 className="mb-4 text-lg font-semibold">Post New Notice</h3>
                        <NoticeForm />
                    </div>

                    {/* Manage Notices List */}
                    <div className="rounded-xl border border-zinc-200 bg-white p-6 shadow-sm dark:border-zinc-800 dark:bg-zinc-900">
                        <h3 className="mb-4 text-lg font-semibold">Manage Notices</h3>
                        <NoticeList />
                    </div>
                </div>

                {/* Recent Activity Column */}
                <div className="rounded-xl border border-zinc-200 bg-white p-6 shadow-sm dark:border-zinc-800 dark:bg-zinc-900">
                    <h3 className="mb-4 text-lg font-semibold">Recent Activity</h3>
                    <div className="space-y-4">
                        {[1, 2, 3].map((i) => (
                            <div key={i} className="flex items-center gap-4">
                                <div className="h-2 w-2 rounded-full bg-blue-500"></div>
                                <div className="flex-1">
                                    <p className="text-sm font-medium">New resource uploaded</p>
                                    <p className="text-xs text-muted-foreground">2 mins ago</p>
                                </div>
                            </div>
                        ))}
                    </div>
                </div>
            </div>
        </div>
    );
}
